import TopSection from '@/src/component/analytics/TopSection'
import React from 'react'
import ProfileMatches from '@/src/component/analytics/ProfileMatch'
import ReportsThisWeek from '@/src/component/analytics/ReportThisWeek'

const index = () => {
  return (
    <div className="min-h-screen w-full bg-gray-50">
      
      {/* FIXED TOP BAR */}
      <TopSection />

      {/* SCROLL CONTENT */}
      <div className="pt-[90px] px-4">
        {/* Line under the top bar - full width */}
        <div className="border-b border-gray-400 w-full mb-4"></div>

        <ProfileMatches />
        <ReportsThisWeek />
      </div>

    </div>
  )
}

export default index
